* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.container {
	display: flex;
	justify-content: space-between;
}

/* Flex Styling */
.d-flex {
	display: flex;
}
.justify-content-center {
	justify-content: center;
}
.justify-content-between {
	justify-content: space-between;
}

/* Sizing */
.w-100 {
	width: 100%;
}
.w-50 {
	width: 50%;
}
.w-25 {
	width: 25%;
}
.position-relative {
	position: relative;
}
.rounded {
	border-radius: 1.25rem !important;
}
.btn:not(:disabled):not(.disabled) {
	cursor: pointer;
}
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled),
button:not(:disabled) {
	cursor: pointer;
}
.btn {
	display: inline-block;
	font-weight: 400;
	color: #212529;
	text-align: center;
	vertical-align: middle;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	background-color: transparent;
	border: 1px solid transparent;
	padding: 0.375rem 0.75rem;
	font-size: 1rem;
	line-height: 1.5;
	border-radius: 0.25rem;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
		border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.text-center {
	text-align: center;
}

.body-container {
	/* display: flex; */
	white-space: nowrap;
	position: relative;
	overflow-x: hidden;
	overflow-y: hidden;
	-webkit-overflow-scrolling: touch;
}
.car-card {
	width: 25%;
	background-color: #ffffff;
	float: none;
	height: 90%;
	margin: 0 0.25%;
	display: inline-block;
	zoom: 1;
}
.car-name-type-text {
	display: flex;
	padding: 10px 0px;
}
.car-img {
	width: 100%;
}
button,
select {
	text-transform: none;
}
button,
input {
	overflow: visible;
}
button,
input,
optgroup,
select,
textarea {
	margin: 0;
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}
.container,
.container-fluid,
.container-lg,
.container-md,
.container-sm,
.container-xl {
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	display: block;
}
.d-flex {
	display: flex;
}
.justify-content-end {
	justify-content: end;
}
.btn {
	border: 0px;
	background-color: 0px;
	margin: 0 0;
	padding: 15px 15px;
	box-sizing: border-box;
}
.btn-filters {
	padding: 5px 15px;
	background-color: #ffffff;
	border: 1px solid #e5e5e5;
	margin: 2px 5px;
	border-radius: 20px;
	text-transform: uppercase;
}
.btn-filters:active,
.btn-filters:active,
.btn-filters:focus {
	background-color: #1c6bba;
	color: #fff;
	border: 0px;
}
.rotate-180 {
	transform: rotate(180deg);
}
.btn.rotate-180 img {
	margin-top: 10px;
}
.mr-1 {
	margin-right: 5px;
}
.car-buttons {
	display: flex;
	justify-content: center;
	text-align: center;
}
.page-link {
	display: flex;
	text-decoration: none;
	padding: 5px 20px;
	color: #1c6bba;
}
.page-link img {
	margin: 0px 5px;
}
.filters-container {
	display: flex;
	padding: 30px 0px;
}
.dot-container {
	display: none;
	justify-content: center;
}
.dot {
	width: 20px;
	height: 20px;
	background-color: #e5e5e5;
	border-radius: 20px;
	margin-right: 5px;
}
.dot.active {
	background-color: #333232;
}
.button-container {
	display: flex;
	justify-content: flex-end;
}
@media (min-width: 1200px) {
	.container,
	.container-lg,
	.container-md,
	.container-sm,
	.container-xl {
		max-width: 1140px;
	}
}
@media (min-width: 1200px) {
	.container,
	.container-lg,
	.container-md,
	.container-sm,
	.container-xl {
		max-width: 1440px;
	}
}

@media (max-width: 992px) {
	.container {
		display: block;
	}
}
@media (max-width: 768px) {
	.car-card {
		width: 75%;
	}
	.filters-container {
		display: block;
	}
	.filters-container .d-flex {
		flex-wrap: wrap;
	}
	.car-name-type-text {
		display: block;
	}
	.dot-container {
		display: flex;
		padding: 40px 0px;
	}
	.button-container {
		display: none;
	}
}
